html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Montserrat", sans-serif;
  font-size: 0.875rem;
  background-color: hsl(30deg, 38%, 92%);
}

h1, h2, h3 {
  margin: 0;
}

p {
  margin: 0;
}

a, a:visited, a:active {
  text-decoration: none;
}

.main {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.product-card {
  text-align: center;
  width: 90%;
  max-width: 21.375rem;
  margin: 1.75rem 0 1.75rem 0;
  background-color: hsl(0deg, 0%, 100%);
  border-radius: 0.625rem;
  overflow: hidden;
}
@media (min-width: 90em) {
  .product-card {
    display: grid;
    grid-template-columns: 1fr 1fr;
    max-width: 37.5rem;
    align-items: center;
  }
}
.product-card__img {
  max-width: 100%;
  height: auto;
  display: block;
}
@media (min-width: 90em) {
  .product-card__img--mobile {
    display: none;
  }
}
.product-card__img--desktop {
  display: none;
}
@media (min-width: 90em) {
  .product-card__img--desktop {
    display: block;
  }
}
.product-card__info {
  padding: 1.375rem;
  text-align: left;
}
@media (min-width: 90em) {
  .product-card__info {
    padding: 1.875rem;
  }
}
.product-card__product {
  font-size: 0.75rem;
  color: hsl(228deg, 12%, 48%);
  font-weight: 500;
  letter-spacing: 0.375rem;
  margin-bottom: 0.9375rem;
}
@media (min-width: 90em) {
  .product-card__product {
    margin-bottom: 1.375rem;
  }
}
.product-card__name {
  width: 90%;
  font-family: "Fraunces", serif;
  font-size: 1.875rem;
  line-height: 1;
  color: hsl(212deg, 21%, 14%);
  margin-bottom: 1.125rem;
}
@media (min-width: 90em) {
  .product-card__name {
    font-size: 2.125rem;
  }
}
.product-card__description {
  color: hsl(228deg, 12%, 48%);
  line-height: 1.6;
  margin-bottom: 1.25rem;
}
.product-card__price-container {
  display: flex;
  align-items: center;
  margin-bottom: 0.875rem;
}
@media (min-width: 90em) {
  .product-card__price-container {
    margin-bottom: 1.625rem;
  }
}
.product-card__price-discount {
  font-family: "Fraunces", serif;
  font-size: 2rem;
  color: hsl(158deg, 36%, 37%);
  padding-right: 1rem;
}
.product-card__price-normal {
  font-size: 0.75rem;
  color: hsl(228deg, 12%, 48%);
  text-decoration: line-through;
}
.product-card__button {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: hsl(158deg, 36%, 37%);
  padding: 1rem 0;
  border-radius: 0.5rem;
  cursor: pointer;
  user-select: none;
}
.product-card__button:hover {
  background-color: #1A4031;
  transition: 100ms ease-in-out;
}
.product-card__button:active {
  background-color: #162921;
  transition: 0s;
}
.product-card__button p {
  color: hsl(0deg, 0%, 100%);
  padding-left: 0.625rem;
}